.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    background-color: rgb(14, 146, 240);
}

.login__main{
    padding: 10px;
    display: flex;
    flex-direction: column;
    text-align: center;
    box-shadow: 10px 5px 5px black;
    border: 2px solid black;
    color: white;
    font-size: 2rem;
    margin-bottom: 10%;
}

.login__img{
   width: 100vw;
   height: 100vh;
}

img{
    width: 100%;
    height: 100%;
}

.login :hover button{
    background-color: black;
    color: white;

}

